align-items: center;\n    gap: 8px;\n    width: 100%;\n\n    * {\n      text-align: center;\n    }\n\n    div {\n      display: flex;\n      justify-content: center;\n      flex-direction: column;\n\n      .in-call-btn {\n        cursor: text;\n\n        span {\n          font-weight: 600;\n        }\n      }\n\n      .speed-btn {\n        position: absolute;\n        top: 0;\n        right: 0;\n\n        border: none;\n        margin: 4px 4px 0 0;\n        padding: 4px 8px !important;\n\n        span {\n          font-size: 10px;\n        }\n      }\n\n      p:not(.end-call__text) {\n        font-size: 18px;\n        font-weight: bold;\n      }\n\n      .end-call__text {\n        color: $--color-danger;\n        font-size: 12px;\n      }\n\n      .waiting-dialer {\n        max-width: 25vw;\n        word-break: break-word;\n        display: flex;\n        flex-direction: column;\n        align-items: center;\n\n        .load-skeleton {\n          .el-skeleton {\n            .is-first {\n              width: 100%;\n            }\n          }\n        }\n      }\n    }\n  }\n\n  .call-queue__container-feedback-time-running-call {\n    display: block;\n    text-align: center;\n  }\n\n  .call-queue__container-call-queue {\n    border: none;\n    box-shadow: 0px 3px 6px #00000029;\n    border-radius: 6px;\n    padding: 8px;\n    display: flex;\n    flex-direction: column;\n    gap: 8px;\n\n    .call-queue__container-call-queue-line {\n      display: flex;\n      flex-direction: row;\n      gap: 8px;\n      justify-content: space-between;\n      align-items: center;\n      gap: 16px;\n\n      .el-button:hover {\n        border-color: white;\n\n        .el-icon-delete::before {\n          color: white;\n        }\n      }\n    }\n  }\n\n  .call-queue__container-empty-queue {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    justify-content: center;\n    gap: 8px;\n\n    .call-queue__container-empty-queue-icon {\n      height: 24px;\n      width: 24px;\n    }\n\n    .call-queue__container-empty-queue-label {\n      font-weight: 600;\n    }\n\n    .el-icon-loading::before {\n      font-size: 24px;\n    }\n  }\n\n  .call-queue__back-button {\n    display: flex;\n    justify-content: center;\n  }\n\n  // div:not(.el-dialog__wrapper):not(.el-collapse):not(.el-collapse>*) {\n  //   z-index: 3000;\n  // }\n}\n\n.back-forward-dispute-confirm {\n  min-width: 50vw;\n}\n","/* Colors\n-------------------------- */\n\n$--color-white: #fff !default;\n$--color-black: #424242 !default;\n$--color-gray: #979797 !default;\n$--color-gray-light-1: mix($--color-white, $--color-gray, 10%) !default;\n$--color-light-gray: #f3f4f5 !default;\n$--color-whatsapp: #075E54 !default;\n\n$--pj-color-darker-blue: #0A0022 !default;\n$--pj-color-dark-blue: #280BA0 !default;\n$--pj-color-blue: #0756e4 !default;\n$--pj-color-light-blue: #018EF0 !default;\n$--pj-color-lighter-blue: #30A9FB !default;\n$--pj-color-white-blue: #F0F9FE !default;\n$--pj-color-disabled-blue: #9ad2f9 !default;\n\n$-pg-color-overlay: #19415F !default;\n\n$--pj-color-red: #CC4148 !default;\n$--pj-color-light-red: #EC767C !default;\n$--pj-color-disable-red: #FFF2F3 !default;\n\n$--color-primary: $--pj-color-blue !default;\n$--color-primary-light-1: mix($--color-white, $--color-primary, 10%) !default;\n$--color-primary-light-2: mix($--color-white, $--color-primary, 20%) !default;\n$--color-primary-light-3: mix($--color-white, $--color-primary, 30%) !default;\n$--color-primary-light-4: mix($--color-white, $--color-primary, 40%) !default;\n$--color-primary-light-5: mix($--color-white, $--color-primary, 50%) !default;\n$--color-primary-light-6: mix($--color-white, $--color-primary, 60%) !default;\n$--color-primary-light-7: mix($--color-white, $--color-primary, 70%) !default;\n$--color-primary-light-8: mix($--color-white, $--color-primary, 80%) !default;\n$--color-primary-light-9: mix($--color-white, $--color-primary, 90%) !default;\n\n$--color-secondary: $--pj-color-light-